import React from 'react'
import {OBJModel} from 'react-3d-viewer'
import Addurl from './Suspension.obj' 

function Fbxviewer() {
  let cameraPosition = 
{ x: 100, 
y: 300, 
z: 600, }; 
  return (  
    <div> 
    <OBJModel 
    //position={cameraPosition} 
    src={ Addurl }
    texPath=""
     //x="500" y="800"
      /> 
    </div>   
  )
}

export default Fbxviewer